  height: 30px;
  text-indent: -99em;
  overflow: hidden; }

.webmain .con .title h2 {
  display: block;
  width: 100px;
  height: 60px;
  background-image: url("../../../images/txt_contitle.gif");
  background-repeat: no-repeat;
