.webmain .activity .group1 {
  margin-bottom: 20px; }

.webmain .activity .group2 {
  margin-bottom: 30px; }

.webmain .activity .inner {
  width: 1230px;
  margin-left: -30px;
  margin-top: -30px; }

.webmain .activity .inner li {
  float: left;
