  margin-top: 30px; }

.webmain .activity .inner img {
  width: 380px;
  height: 285px; }

.webmain .activity .inner .tags {
  position: absolute;
  width: 350px;
  height: 35px;
  padding: 15px;
  margin-top: -65px;
  color: #fff;
  background: url("../../../images/space.png") 0 0 repeat;
