.webmain .activity .inner .desc div {
  margin-top: 8px; }

#pagelist {
  bottom: -45px; }

.wrapper {
  position: relative;
  width: 100%;
  height: 100%; }

.mask {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.34);
  display: none; }

.mask .popbox {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 575px;
  height: 775px;
  margin: auto;
  background-color: #fff;
  border: 5px solid #666; }

.mask .popbox h3 {
  font-size: 16px;
  line-height: 50px;
  text-indent: 20px; }

.mask .popbox input {
  margin-left: 30px;
  margin-bottom: 24px;
  height: 35px;
  text-indent: 15px;
  background-color: #fff;
  border: 1px solid #ccc; }

.mask .popbox textarea {
  width: 500px;
  height: 108px;
  margin-left: 30px;
  margin-bottom: 5px;
  text-indent: 15px;
  font-size: 14px;
  line-height: 30px;
  background-color: #fff;
  border: 1px solid #ccc;
  overflow-y: hidden; }

.mask .popbox select {
  width: 118px;
  height: 35px;
  text-indent: 15px;
  color: #999;
  background-color: #fff; }

.mask .popbox select option {
  text-indent: 15px; }

.mask .popbox .pic {
  position: relative;
  width: 500px;
  height: 35px;
  display: block;
  margin-left: 30px;
  border: 1px solid #ccc;
  margin-bottom: 24px;
  font-size: 12px;
  color: #999;
  line-height: 35px;
  text-indent: 15px;
  overflow: hidden; }
  .mask .popbox .pic input {
    position: absolute;
    left: 0;
    margin-left: 0px;
    height: 35px;
    margin-top: 5px;
    border: 0;
    width: 500px; }

.mask .popbox .camera {
  display: inline-block;
  position: absolute;
  width: 35px;
  height: 35px;
  margin-left: -40px;
  margin-top: 1px;
  background: url("../../../images/icon_camera.jpg") 0 0 no-repeat;
  cursor: pointer; }

.mask .popbox .activeName {
  width: 500px; }

.mask .popbox .activeAdress {
  margin-bottom: 0;
  border: 0;
  margin-left: 0; }

.mask .popbox .border {
  margin-left: 30px;
  border: 1px solid #ccc;
  margin-bottom: 24px; }

.mask .popbox .border {
  width: 500px; }

.mask .popbox .border:before {
  content: "";
  border-right: 1px solid #ccc;
  height: 20px;
  position: absolute;
  margin-left: 115px;
  margin-top: 10px; }

.mask .popbox .fromTime {
  width: 240px; }

.mask .popbox .toTime {
  margin-left: 0;
  width: 240px; }

.mask .popbox .submit {
  position: absolute;
  width: 245px;
  margin-left: 155px;
  background-color: #000;
  color: #fff; }

.close {
  position: absolute;
  top: 10px;
  left: 545px; }
